<template>
  <div class="pictures">
    <div v-for="index in indices" :key="index" class="pictures">
      <img :src="getImagePath(index + 1)" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  name: "PictureShow",
  data() {
    return {
      indices: Array.from({ length: 17 }, (_, i) => i),
    };
  },
  methods: {
    getImagePath(index) {
      return require(`@/assets/img/img/${index}.jpg`);
    },
  },
};
</script>

<style scoped>
@import url("@/assets/css/zdy/pictureShow.less");
</style>